<template>
  <div class="cate-div">
    <div  class="head">
        <div class="categorylist">
            <div class="nav2">
                <a v-for="item in data.category" v-on:click="onClickMe(item.id)" :class="{'on':cateid.indexOf(item.id)==0}">{{item.catename}}</a>
            </div>
        </div>
        <div class="search"><i class="icon iconfont icon-sousuo"></i></div>
    </div>
    <div class="flter">
      <ul>
        <li><i class="icon iconfont icon-xiangxiajiantou"></i></li>
        <li><i class="icon iconfont icon-xiangxiajiantou"></i></li>
        <li><i class="icon-down-small"></i></li>
        <li><i class="icon-down-small"></i></li>
        <li></li>
      </ul>
    </div>
  </div>
</template>
<!--
<style  lang="scss" scoped rel="stylesheet/scss">
//csscomponent
@import '../../assets/css/product/category.scss';
</style>
-->
<script>
import '../../assets/css/product/category.scss';
//csscss
export default {
  name:"category",
  props:["cateid"],
  data(){
      return {
          data:{
              category:[
              {
                id:"c003",
                catename:"" ,
                child:[
                  {
                    id:"c003001",
                    catename:""
                  },
                  {
                    id:"c003002",
                    catename:""
                  },
                  {
                    id:"c003003",
                    catename:""
                  }
                ]
              },{
                id:"c001",
                catename:"",
                child:[
                    {
                id:"c001001",
                catename:"1"  
                    },
                    {
                id:"c001001",
                catename:"2"  
                    }
                ]
              },
              {
                id:"c002",
                catename:"" 
              },
              {
                id:"c004",
                catename:"" 
              },
              {
                id:"c005",
                catename:"" 
              },
              {
                id:"c006",
                catename:"" 
              },
              {
                id:"c007",
                catename:"" 
              }]
            }
      }
  },
  methods:{
    onClickMe: function(id){
      // console.log(id);
      // $(event.currentTarget).addClass("on").siblings("a").removeClass("on");
      this.$parent.cateid=id;
      this.$parent.getProductlist(id);
      // console.log(this.$parent);  //  
      // console.log(this.$parent.cateid);  //cateid  
      // this.$emit('child-say',id);
    }
  }
}
</script>



