// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'


import axios from 'axios'
import VueAxios from 'vue-axios'
import Qs from 'qs'
import Vuex from 'vuex'

import '@/assets/css/base/reset.css'
// import '@/assets/css/normalize.css'
import '@/assets/bootstrap/css/bootstrap.min.css'
//css appcss
import '@/assets/iconfonts/iconfont.css'
// import '@/assets/stylus/index.styl'

import 'lib-flexible/flexible'
import $ from 'jquery'
window.jQuery = $;
window.$ = $;
// import layer from 'vue-layer'
// Vue.prototype.$layer = layer(Vue);
// import 'vue-layer-mobile/need/layer.css'
// import layer from 'vue-layer-mobile'
// Vue.use(layer)
import VueLazyLoad from 'vue-lazyload'
Vue.use(VueLazyLoad,{
    error:'./static/cry.png',
    loading:'./static/loading.gif'
})
require("./assets/lib/layer/mobile/need/layer.css");
require("./assets/lib/layer/mobile/layer.js");
import App from './App'
import store from './store/store'
import router from './router'


Vue.config.productionTip = false //



// #axios
var axios_instance = axios.create({
    // #configtransformRquest
    // #Qs.stringify
    transformRequest: [function(data) {
            data = Qs.stringify(data);
            return data;
        }] //,
        //       // `transformResponse`  then/catch 
        //     transformResponse: [function (data) {
        //       //  data 

    //       return data;
    //     }],
    //     timeout: 1000,
    // // #Content-Type
    //     headers:{'Content-Type':'application/x-www-form-urlencoded'}
})

//axios
axios_instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

//post
axios_instance.interceptors.request.use((config) => {
        config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
        return config;
    }, (error) => {
        return Promise.reject(error);
    })
    //
axios_instance.interceptors.response.use((res) => {
    if (res.status != 200) {
        alert("");
        return Promise.reject(res);
    }
    return res.data;
}, (error) => {
    return Promise.reject(error);
})

// axiosget post
// Vue.prototype.axi={
//   post:function(url,params){
//     let parameters=Qs.stringify(params);
//     return new Promise((resolve,reject)=>{
//       axios.post()
//     })
//   },
//   get:function(){

//   }
// }
Vue.use(VueAxios, axios_instance)
    // Vue.use(VueAxios,axios);


/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})


